@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* magnific popup */
.mfp-bg {
	background-color: #3e3e3d; }
/*
 *
 *====== Zoom effect ====== */
.mfp-zoom-in {
	/* start state */
	.mfp-with-anim {
		opacity: 0;
		transition: all 0.2s ease-in-out;
		transform: scale(0.8); }

	&.mfp-bg {
		opacity: 0;
		transition: all 0.3s ease-out; }

	/* animate in */
	&.mfp-ready {
		.mfp-with-anim {
			opacity: 1;
			transform: scale(1); }

		&.mfp-bg {
			opacity: 0.5; } }

	/* animate out */
	&.mfp-removing {
		.mfp-with-anim {
			transform: scale(0.8);
			opacity: 0; }

		&.mfp-bg {
			opacity: 0; } } }

/* magnific popup end */
